@import '../../common/styles/sass/variables';
@import '../../common/styles/sass/mixins';


.nav {
  border: 1px solid;
  display: flex;
  justify-content: space-between;
  column-gap: 30px;

  & a {
    text-decoration: none;
    @include openSansText(16px, 400, $mainTextColor);

  }
  a:hover {
    color: $primaryColor;
  }
}




